<template>
  <v-container>
    <v-row align="center">
      <v-col cols="12" xs="12" sm="4">
        <Logo />
      </v-col>
      <v-col cols="12" xs="12" sm="8">
        <h1
          class="header-text"
          :slass="
            $vuetify.theme.dark
              ? 'dark'
              : 'light'
          "
        >
          <span v-text="HEADER_NAME_SHORT" />
          <span>&nbsp;</span>
          <Link :link="`${API_URL}/${selectedApi}`" :text="selectedApi" />
          <span>&nbsp;</span>
          <span v-text="HEADER_NAME_POSTFIX" />
        </h1>
      </v-col>
    </v-row>

    <v-row style="position: relative; z-index: 2;">
      <v-col cols="12" xs="12" sm="4">
        <v-select
          v-model="selectedApi"
          :items="SEARCH_API_LIST"
          item-text="api"
          item-value="api"
          :label="`What you search, ${role}? May the Force be with you`"
          dense
        />
      </v-col>
      <v-col cols="12" xs="12" sm="4">
        <v-select
          v-model="selectedField"
          :items="selectedFields"
          label="Selected Field"
          dense
        />
      </v-col>
      <v-col cols="12" xs="12" sm="4" style="position: relative;">
        <v-text-field
          v-model="search"
          :label="`Search ${selectedApi}`"
          :loading="isLoading"
          clearable
          dense
          @input="onInput"
        />
        <DropList
          class="drop-list"
          v-if="items.length && isShownDropDown"
          :items="items"
          :search="search"
          :selected-api="selectedApi"
          :selected-field="selectedField"
          @select="onSelect"
        />
      </v-col>
    </v-row>

    <v-row style="position: relative;">
      <v-col>
        <template v-if="items.length && result !== defaultResult">
          <template v-if="imgURL">
            <div class="wrapper">
              <div class="img" role="img" :aria-label="selectedApi">
                <a
                  href
                  @click.prevent="isDialogShow = !isDialogShow"
                  @keyup="isDialogShow = !isDialogShow"
                >
                  <img
                    v-for="item in 2"
                    :key="item"
                    :src="imgURL"
                    :alt="selectedApi"
                    :onerror="`this.onerror=null;this.src='${IMG_PLACEHOLDER}';`"
                  />
                </a>
              </div>
            </div>
            <Dialog
              class="my-5"
              :search="search"
              :result="result"
              :is-dialog-show="isDialogShow"
              @dialog="onDialog"
            />
          </template>
        </template>
        <SWCrawlText
          class="sw-crawl-text"
          :class="{ mobile: $vuetify.breakpoint.smAndDown }"
        />
        <template v-if="!$vuetify.breakpoint.smAndDown">
          <Mandala />
          <Mandala :class-name="'right'" />
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// import { IS_DEV } from '@/state'
// import RESULTS from '@/state/fixtures'
import {
  API_URL,
  RESOURCE_URL,
  IMG_PLACEHOLDER,
  SEARCH_API_LIST,
  getDataFromApi,
} from '@/utils/getDataFromApi'
import { getIDfromApiUrl } from '@/utils/transformData'
import createJSON from '@/utils/createJSON'
import Logo from '@/components/Logo.vue'
import DropList from '@/components/DropList.vue'
import Link from '@/components/Link.vue'
import SWCrawlText from '@/components/SWCrawlText.vue'
import Dialog from '@/components/Dialog.vue'
import Mandala from '@/components/Mandala.vue'

const INPUT_DELAY = 500

const createInitialState = () => ({
  items: [],
  API_URL,
  HEADER_NAME_SHORT: process.env.VUE_APP_NAME_SHORT,
  HEADER_NAME_POSTFIX: process.env.VUE_APP_NAME_POSTFIX,
  IMG_PLACEHOLDER,
  SEARCH_API_LIST,
  selectedApi: SEARCH_API_LIST[0].api,
  selectedField: SEARCH_API_LIST[0].searchFields[0],
  selectedFields: SEARCH_API_LIST[0].searchFields,
  search: '',
  timeout: null,
  isLoading: false,
  isShownDropDown: false,
  defaultResult: '{}',
  imgURL: null,
  isDialogShow: false,
})

export default {
  name: 'AppForm',
  components: {
    Logo,
    DropList,
    Link,
    SWCrawlText,
    Dialog,
    Mandala,
  },
  props: {
    role: {
      type: String,
      default: '',
    },
  },
  data: () => createInitialState(),
  computed: {
    result() {
      const { items, selectedField, search } = this

      if (!items.length) {
        this.clearResult()
        return ''
      }

      const findedSelected = items.find((item) => item[selectedField] === search)

      if (!findedSelected) {
        this.clearIMGURL()
        return ''
      }

      this.setIMGURL(findedSelected.url)

      const result = createJSON(findedSelected)

      return result
    },
  },
  watch: {
    selectedApi() {
      this.setSearchField()
    },
  },
  methods: {
    setSearchField() {
      const searchField = SEARCH_API_LIST.find(
        ({ api }) => api === this.selectedApi,
      ).searchFields

      const selectedField = searchField[0]

      this.selectedField = selectedField
      this.selectedFields = searchField

      this.clearSearch()
    },
    onSelect(select) {
      this.search = select
      this.isShownDropDown = false
      this.timeout = null
    },
    onInput(event) {
      if (!event) return this.clearSearch()

      this.isShownDropDown = true

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => this.getData(), INPUT_DELAY)

      return this.timeout
    },
    async getData() {
      this.isLoading = true
      const response = await getDataFromApi(this.selectedApi)
      /* const items = IS_DEV
        ? RESULTS
        : response?.results */
      const items = response?.results
      if (items.length) {
        this.items = items
      }
      this.isLoading = false
    },
    setIMGURL(url) {
      const id = getIDfromApiUrl(url)

      const { imgApiPath } = SEARCH_API_LIST.find(
        ({ api }) => api === this.selectedApi,
      )

      const imgURL = `${RESOURCE_URL}/assets/img/${imgApiPath}/${id}.jpg`

      this.imgURL = imgURL
    },
    onDialog(value) {
      this.isDialogShow = value
    },
    clearIMGURL() {
      this.imgURL = null
    },
    clearResult() {
      this.defaultResult = '{}'
    },
    clearSearch() {
      this.search = ''
      this.items = []
      this.timeout = null
    },
  },
}
</script>

<style>
.header-text {
  font-size: clamp(2vw, 2.5rem, 100%);
  font-weight: 700;
}

.header-text.dark {
  color: #fff;
  text-shadow: 3px -1px 14px rgb(255 255 255 / 0.6);
}

.header-text.light {
  color: #fff;
  text-shadow: 3px 1px 14px rgb(0 0 0 / 0.6);
}

.drop-list {
  position: absolute !important;
  right: 0.8rem;
  left: 0.8rem;
}

.drop-list .theme--dark.v-list {
  color: #fff;
  background-color: rgb(30 30 30 / 0.4) !important;
}

.drop-list .theme--light.v-list {
  color: rgb(0 0 0 / 0.87);
  background-color: rgb(255 255 255 / 0.05) !important;
}

.wrapper {
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: grid;
  place-content: center;
  place-items: center;
  width: 100%;
  height: 100%;
}

.img {
  position: relative;
  width: clamp(9vw, 60vh, 90vw);
  height: clamp(9vw, 60vh, 90vw);
  cursor: pointer;
  opacity: 0.6;
}

.img img {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 5px 8px rgb(255 255 255 / 0.05);
  opacity: 0.5;
  transition: 0.5s ease-in-out;
  object-fit: cover;
}

.img img:nth-child(2) {
  z-index: -1;
  filter: blur(6px);
  opacity: 0.25;
  transform: scale(1.15);
}

.img:hover img:nth-child(2) {
  filter: blur(4px);
  transform: scale(1.1);
}

.sw-crawl-text {
  position: absolute;
  top: 10vh;
  right: 2vh;
  bottom: 2vh;
  left: 2vh;
  z-index: 0;
}

.sw-crawl-text.mobile {
  top: -10vh;
  bottom: -5vh;
}
</style>
